#contact {
  padding: 5rem 0 0 0;
  
}

#contact > div {
  display: flex;
  justify-content: space-between;
}

form {
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  gap: 1rem;
}

form input,
form textarea {
  width: 80%;
  height: 3.5rem;
  padding: 1rem;
  font-size: 150%;
  resize: none;
  box-shadow: 2px 1px 9px .3px #927983
}

form textarea {
  height: 10rem;
}

form input:last-child:hover {
filter: brightness(.9);
cursor: pointer;
}

.contact-container {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  margin: 6rem 0;
  width: 25%;
  align-items: flex-end;
  padding: 1rem;
  border-right: .5rem solid var(--color-primary);
  padding-bottom: 6rem;
}

.tel {
  display: flex;
  width: fit-content;
  font-size: 1.3rem;
  gap: 1rem;
}

.tel div {
  color: var(--color-primary);
  align-self: center;
}

.tel a {
  color: var(--color-black);
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-primary);
  height: 5rem;
}

footer div {
  color: white;
  display: flex;
}

footer div img {
  width: 6rem;
}

@media screen and (max-width: 600px) {
  .contact-container {
    gap: 1.5rem;
  }
}
