#about {
  padding-top: 11rem;
}

.about-container {
  display: grid;
  grid-template-columns: 35% 53%;
  gap: 12%;
}

.about-portrait {
  height: fit-content;
  max-height: 42rem;
  overflow: hidden;
  filter: grayscale(0.65);
}

.about-right p {
  margin: 3rem 0;
  text-indent: 1.5rem;
}


@media screen and (max-width: 1024px) {
  .about-left {
    display: none;
  }

  .about-container {
    grid-template-columns: 1fr;
  }

  .about-right h2 {
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .about-cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

}
